window.docsearch = require('docsearch.js');
window.Vue = require('vue');

Vue.config.productionTip = false;

import hljs from 'highlight.js/lib/highlight';

// const languageFiles = require.context(
//     'highlight.js/lib/languages/',
//     false,
//     /(json|css).js$/
// );

// languageFiles.keys().forEach(filename => {
//     hljs.registerLanguage(
//         filename.split('.')[1].split('/')[1],
//         languageFiles(filename)
//     );
// });

hljs.registerLanguage('json', require('highlight.js/lib/languages/json'));
hljs.registerLanguage('css', require('highlight.js/lib/languages/css'));
hljs.registerLanguage('html', require('highlight.js/lib/languages/xml'));
hljs.registerLanguage('scss', require('highlight.js/lib/languages/scss'));
hljs.registerLanguage('php', require('highlight.js/lib/languages/php'));
hljs.registerLanguage(
    'markdown',
    require('highlight.js/lib/languages/markdown')
);
hljs.registerLanguage('yaml', require('highlight.js/lib/languages/yaml'));
hljs.registerLanguage(
    'javascript',
    require('highlight.js/lib/languages/javascript')
);
hljs.registerLanguage('bash', require('highlight.js/lib/languages/bash'));

import NavigationToggle from './components/navigation-toggle.vue';
import Navigation from './components/navigation.vue';

new Vue({
    components: { Navigation, NavigationToggle },

    mounted() {
        document.querySelectorAll('pre code').forEach(block => {
            hljs.highlightBlock(block);
        });
    },
}).$mount('#vue-app');



